import React, { useState } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import Dialog from '@/components/dialog/Dialog.js'
import scss from './promotions.module.scss'

const cx = classNames.bind(scss)

function Promotions(props) {
  const [visible, setVisible] = useState(false)
  const params = {
    title: '测试组件引用',
    clickCloseCallback() {
      setVisible(false)
    }
  }
  return (
    <div>
      <Dialog params={params} visible={visible}>
        <div>
          <h1 style={{
            width: 600,
            height: 400
          }}>Dialog</h1>
        </div>
      </Dialog>
      <button onClick={() => setVisible(true)}>openDialog</button>
      <button onClick={() => setVisible(false)}>hideDialog</button>
      <h1 onClick={() => props.history.push('/')}>Promotions</h1>
    </div>
  )
}

Promotions.propTypes = {
  history: PropTypes.object.isRequired
}

export default Promotions
